.info-form {
  padding: 1rem 5rem;
 
  background-color: #8a3333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #dbdbdb;
  text-transform: uppercase;
}
.info-form-input {
  display: flex;
  justify-content: space-evenly;
}

.info-form-input__controls {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;

  width: 20rem;
  max-width: 100%;
  margin-top: 0.5rem;
}

.info-form-input__control {
  padding: 0.5rem;
}

.info-form-input__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.info-form-input__control input,
.info-form-input__control select,
.info-form-input__control textarea {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #dbdbdb;
  width: 20rem;
  max-width: 100%;
  color: rgb(75, 74, 74);
}

.info-form-actions {
  text-align: center;
}

.info-form-actions button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #4d1717;
  background-color: rgb(77, 23, 23);
  color: #fff;
  border-radius: 12px;
  margin-right: 4rem;
}

.info-form-actions button:hover,
.new-expense button:active {
  background-color: #fff;
  color: rgb(138, 51, 51);
  border-color: #fff;
}
