@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0 auto;
  background-color: whitesmoke;
  height: 100vh;
}

.card {
  padding-top: 0;
  padding-bottom: 2rem;
}

.infos {
  padding: 0 1rem;
  background-color: #585858;
  border-radius: 1.2rem;
  margin: 1rem;
  color: #dbdbdb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.infos h2:nth-child(3) {
  width: 30rem;
}

.infos h2:nth-child(2) {
  width: 20rem;
}

.infos h2:first-child,
.infos h2:last-child {
  width: 10rem;
}

.header {
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  padding: 2rem 4rem;

  display: flex;
  justify-content: space-between;
  height: 7rem;
  position: relative;
}

.header-title {
  align-self: center;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  left: 49rem;
}

.header-title h1 {
  letter-spacing: 5px;
  padding-bottom: 0;
  margin-bottom: 0;
}

.header-title h3 {
  letter-spacing: 5px;
  padding-top: 0;
  margin-top: 0;
}

.header-search input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.info-form {
  padding: 1rem 5rem;
 
  background-color: #8a3333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #dbdbdb;
  text-transform: uppercase;
}
.info-form-input {
  display: flex;
  justify-content: space-evenly;
}

.info-form-input__controls {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;

  width: 20rem;
  max-width: 100%;
  margin-top: 0.5rem;
}

.info-form-input__control {
  padding: 0.5rem;
}

.info-form-input__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.info-form-input__control input,
.info-form-input__control select,
.info-form-input__control textarea {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #dbdbdb;
  width: 20rem;
  max-width: 100%;
  color: rgb(75, 74, 74);
}

.info-form-actions {
  text-align: center;
}

.info-form-actions button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #4d1717;
  background-color: rgb(77, 23, 23);
  color: #fff;
  border-radius: 12px;
  margin-right: 4rem;
}

.info-form-actions button:hover,
.new-expense button:active {
  background-color: #fff;
  color: rgb(138, 51, 51);
  border-color: #fff;
}

.app {
  padding: 0 0 1rem 0;
  background-color: rgb(31, 31, 31);

  border-radius: 1.2rem;
  margin: 1rem auto;
  max-width: 80%;
  color: #dbdbdb;
}

.info-header {
  display: flex;
  justify-content: space-between;
  background-color: #4d1717;
  border-radius: 12px;
  padding: 0 2rem 1rem 2rem;
  position: relative;
}

/* .info-header h2:first-child {
  margin-left: 2rem;
}*/

.info-header h2:nth-child(2) {
  position: absolute;
  left: 25rem;
}

.info-header h2:nth-child(3) {
  position: absolute;
  left: 55rem;
}

.footer {
  background-color: #8a3333;
  color: whitesmoke;
  padding: 1rem;
}

.footer-details {
  display: flex;
  justify-content: center;
}

.footer-details img {
  width: 4rem;
  height: 2rem;
  border-radius: 50%;
  margin-right: 2rem;
}

