.header {
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  padding: 2rem 4rem;

  display: flex;
  justify-content: space-between;
  height: 7rem;
  position: relative;
}

.header-title {
  align-self: center;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  left: 49rem;
}

.header-title h1 {
  letter-spacing: 5px;
  padding-bottom: 0;
  margin-bottom: 0;
}

.header-title h3 {
  letter-spacing: 5px;
  padding-top: 0;
  margin-top: 0;
}

.header-search input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}
