.infos {
  padding: 0 1rem;
  background-color: #585858;
  border-radius: 1.2rem;
  margin: 1rem;
  color: #dbdbdb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.infos h2:nth-child(3) {
  width: 30rem;
}

.infos h2:nth-child(2) {
  width: 20rem;
}

.infos h2:first-child,
.infos h2:last-child {
  width: 10rem;
}
